<template id="">
  <div class="">
    <button type="button" name="button" v-on:click="open">Network Settings</button>

    <div class="networksettings" v-bind:class="{'hide':!this.visible}">
      <div class="wrapper">
        <label for="">
          IP-Adresse:
          <input required pattern="^([0-9]{1,3}\.){3}[0-9]{1,3}$" v-model="ipadress" v-on:keyup="change">
        </label>
        <label for="">
          Subnetmask:
          <input required pattern="^([0-9]{1,3}\.){3}[0-9]{1,3}$" v-model="mask">
        </label>
        <label for="">
          Gateway:
          <input required pattern="^([0-9]{1,3}\.){3}[0-9]{1,3}$" v-model="gateway">
        </label>
        <label for="">
          DNS-Search:
          <input required pattern="^([0-9]{1,3}\.){3}[0-9]{1,3}$">
        </label>
        <div class="wrapper-btn">
          <button type="button" name="button">Apply</button>
          <button type="button" name="button" v-on:click="close">Abort</button>
        </div>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
  export default{
    name: 'networksettings',
    methods:{
      open(){
        this.visible = true;
      },
      close(){
        this.visible = false;
      },
      change(){
        if(this.ipadress.startsWith('192.168'))
          this.mask = '255.255.255.0'
        if(Array.from(this.ipadress).filter(s => s == '.').length == 3)
            this.gateway = this.ipadress.slice(0, this.ipadress.lastIndexOf('.') + 1 ) + '1';
      }
    },
    data(){
      return{

        visible: false,
        ipadress: '',
        mask: '',
        gateway: ''
      }
    }
  }
</script>
<style media="screen" scoped>
  .networksettings{

    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0,0,0,0.8);

  }
  .wrapper{
    background-color: white;
    flex: 0 0 auto;
    display: flex;
    flex-direction: column;
    margin: 15.0%;
  }
  .networksettings label{
    flex: 0 0 auto;
    margin: 10pt;
  }
  .networksettings input{
    font-size: 20pt;
    display: block;
    margin: 0 auto ;

  }
  .networksettings button{
    margin: 10pt;
  }
  .wrapper-btn{
    display: flex;
    flex-direction: row-reverse;
    margin: auto;

  }
  .hide{
    display: none !important;
  }
</style>
