0<template id="">
  <div class="requests">
    <div class="header">
      <div class="cell" v-for="header in requests.header"  :key="header.id" v-bind:class="{'name':(header.name == 'name')} "><a>{{header.text}}</a></div>
      <div class="placeholder">

      </div>
    </div>

    <request v-for="item in requests.items"  :key="item.id"
          v-bind:active="item.active"
          v-bind:markiert="item.markiert"
          v-bind:items="item.items"
          v-on:mark="mark(item)"
          v-on:values-change="$emit('table-changed')"
          v-on:active-change="change_active(item)"
          />

    <div class="wrapper">
      <button type="button" name="button" v-on:click="$emit('addRequest')">Add Request</button>
    </div>
  </div>
</template>
<script type="text/javascript">
import Vue from 'vue'
import request from './request.vue'

export default{
  name: 'requests',
  components: { request },
  props: ['requests'],
  methods:{
    mark(item){
      if(this.requests.items.filter(request => request.markiert).length <= 0)
        this.$emit('demark');

      let item_markiert = item.markiert;
      this.requests.items.forEach((elem, i) =>{
        elem.markiert = (elem.markiert == Vue.prototype.$shiftPressed) && elem.markiert
        if(this.requests.items.length == (i + 1) && !item_markiert){
          item.markiert = true;
        }
      });

    },
    change_active(item){
      item.active = !item.active
      this.$emit('change-active', item)
    }
  },
  data(){
    return{
      marked_item: false
    }
  },
  created(){
  }
}


</script>
<style media="screen" scoped>
.requests{
  display: flex;
  flex-direction: column;
}

.header{
  display: flex;
  flex-direction: row;
}
.header a{
  font-weight: bold;
}
.wrapper{
  display: flex;
  flex-direction: row-reverse;
}
.cell{
  flex: 1 0 0;
}
.cell.name{
  flex-grow: 2;
}
.placeholder{
  flex: 0 0 20pt;
}
</style>
