<template id="">
  <div class="">
    <div class="wrapper_buttons">
      <button type="button" name="button" v-on:click="apply">Anwenden</button>
      <button type="button" name="button">Laden</button>
      <button type="button" name="button">Restart</button>
    </div>

    <header>
      <list class="navbar" v-bind:items="this.header"/>
    </header>
    <div class="main">
      <requests
      v-for="requestpanel in requestPanels"  :key="requestpanel.id"
      v-bind:requests="requestpanel.requests"
      v-on:demark="demark"
      v-on:addRequest="addRequest(requestpanel)"
      v-on:table-changed="tablechanged"
      v-on:change-active="change_active"
      />
    </div>
  </div>
</template>
<script type="text/javascript">
  import Vue from 'vue'
  import list from './VueComponents/list.vue'
  import requests from './requests.vue'
  import { v4 as uuidv4 } from 'uuid'

  export default{
    name: 'requestPage',
    components: { list, requests },
    methods:{
      apply(){
        console.log('Table Changed');
        let table = []
        for (var requests of this.requestPanels) {
          for (var request of requests.requests.items) {
            let line = {};
            for (var i = 0; i < requests.requests.header.length; i++) {
              let value;
              if(i == 0)
                value = request.active
              else
                value = request.items[i - 1].text;
              let key = requests.requests.header[i].name;
              line[key] = value;
            }

            table.push(line)

            if(line.type == 'static')
              table.push({type: 'time', name:'time'})
          }
        }
        console.log(table);
        Vue.prototype.$socket.client.emit('set_Table', {table: table})
      },
      demark(){
        this.requestPanels.forEach( panel =>
          panel.requests.items.forEach( item => item.markiert = false )
        );
      },
      addRequest(requestpanel){
        let h_length = (requestpanel.requests.header.length - 2)
        let newItems = [{
          id: uuidv4(),
          text: requestpanel.type,
          noteditable:true,
          active: true
        }]

        for (var i = 0; i < h_length; i++) {
          newItems.push({id: uuidv4(), text: '', edit:false, name: requestpanel.requests.header[i+2].name})
        }

        requestpanel.requests.items.push({
            id: uuidv4(), active: true, markiert:false,
            items: newItems
          });
        console.log(requestpanel.requests.items);
      },
      tablechanged(){


      },
      change_active(main_item){
        if(!main_item.markiert){
          console.log('not markiert');
          return;
        }
        this.requestPanels.forEach((panel)=>{
          panel.requests.items.filter(item => item.markiert).forEach(item => {
            item.active = main_item.active;
          });
        });
      }
    },
    data(){
      return{
        header:[
          {id: 0, text: 'Requests', active: true},
          {id: 1, text: 'Scanner', active: false}
        ],
        requestPanels:[{
            id: uuidv4(),
            type: 'static',
            requests: {
                header:[{
                  id: uuidv4(),
                  text: 'Active',
                  name: 'active'
                },{
                  id: uuidv4(),
                  text: 'Type',
                  name: 'type'
                },{
                  id: uuidv4(),
                  text: 'Name',
                  name: 'name'
                },{
                  id: uuidv4(),
                  text: 'Unit',
                  name: 'unit'
                },{
                  id: uuidv4(),
                  text: 'Value',
                  name: 'value'
                }
              ],
              items:[]
            }
        },{
            id: uuidv4(),
            type: 's7',
            requests: {
              header:[{
                id: uuidv4(),
                text: 'Active',
                name: 'active'
              },{
                id: uuidv4(),
                text: 'Type',
                name: 'type'
              },{
                id: uuidv4(),
                text: 'Name',
                name: 'name'
              },{
                id: uuidv4(),
                text: 'Unit',
                name: 'unit'
              },{
                id: uuidv4(),
                text: 'IP-Adresse',
                name: 'ip'
              },{
                id: uuidv4(),
                text: 'DB',
                name: 'db'
              },{
                id: uuidv4(),
                text: 'Offset',
                name: 'offset'
              },{
                id: uuidv4(),
                text: 'Länge',
                name: 'length'
              },{
                id: uuidv4(),
                text: 'Datatype',
                name: 'datatype'
              }],
              items:[]
            }
          },{
            id: uuidv4(),
            type: 'analog',
            requests: {
              header:[{
                id: uuidv4(),
                text: 'Active'
              },{
                id: uuidv4(),
                text: 'Type'
              },{
                id: uuidv4(),
                text: 'Name'
              },{
                id: uuidv4(),
                text: 'channel'
              }
              ],
              items:[]

            }
          }
        ]
        ,
        cache: [],
        parent_Index: -1
      }
    },
    created(){

      Vue.prototype.$copyline = ()=>{
        this.requestPanels.forEach((panel, i) =>{
          let toCopy = panel.requests.items.filter(item => item.markiert )

          //die copierten brauchen neue ids deep Copy
          if(toCopy.length > 0) {
            this.parent_Index = i;
            this.cache = toCopy;
            console.log('gespeichert');
          }
        });
      },
      Vue.prototype.$pasteline = ()=>{
        this.cache = JSON.parse(JSON.stringify(this.cache));
        for (const line of this.cache)
          line.id = uuidv4();

        this.cache.forEach(line => {
          if(this.parent_Index >= 0)
            this.requestPanels[this.parent_Index].requests.items.push(line);
        });
      },
      Vue.prototype.$cutline = ()=>{
        this.requestPanels.forEach((panel, i)=>{
            let toCopy = panel.requests.items.filter(item => item.markiert);
            let toShow = panel.requests.items.filter(item => !item.markiert)

            if(toCopy.length > 0) {
              this.parent_Index = i;
              this.cache = toCopy;
              panel.requests.items = toShow;
            }

        });
      }

      Vue.prototype.$socket.$subscribe('get_Table_back', (data)=>{
        for (var line of data.table) {
          console.log('line', item, line);
          let reqPanel = this.requestPanels.filter( item => item.type == line.type)
          if(reqPanel.length == 0)
            continue
          reqPanel = reqPanel[0];

          const ITEMS = new Array(Object.keys(line).length - 1);
          let ACTIVE = false
          for (var item in line) {
            let index = 0;

            for(let a = 0; a < reqPanel.requests.header.length; a++){

              if(reqPanel.requests.header[a].name == item)
                index = a - 1
            }

            console.log(item ,' index ' + index)
            if(item == 'active' || item == 'not_active'){
              ITEMS.row_active = line[item]
              if (item == 'active' ) {
                ACTIVE = line[item]
              }

            }else if (item == 'type')
              ITEMS[index] = {id:uuidv4(), text: line[item], name: item};
            else
              ITEMS[index] = {id:uuidv4(), text: line[item], name: item, edit:false};
              console.log(ITEMS)

          }
          console.log('Items' , ITEMS);
          reqPanel.requests.items.push({id: uuidv4(), markiert:false, active:ACTIVE, items:ITEMS })//in die table


        }
      });
    }

  }
</script>
<style media="screen">
header{

}
.wrapper_buttons{
  margin: 20px 0;

}
.wrapper_buttons button{
  margin: 0 20pt;
}
</style>
