<template id="">
  <div class="row" v-bind:class="{'markiert':this.markiert}">
    <checkbox class="cell font-20pt" v-on:input="$emit('active-change')" v-bind:value="this.active"/>

    <div class="cell" v-for="(item, index) in this.items"  :key="item.id" v-bind:class="{'name':item.name=='name'}" v-on:click.self="mark(item, index)" >
      <a v-bind:class="{'hide':item.edit }" v-on:click="edit(item, index)">{{item.text}}</a>
      <input
        ref="input"
        type="text"
        v-bind:class="{'hide':!item.edit }"
        v-model="item.text"
        v-on:keydown="unset_tab"
        v-on:keydown.tab.prevent="next(item)"
        v-on:keyup.enter="enter"
        @blur="enter"
        />
    </div>

    <button class="" type="button" name="button"></button>
  </div>
</template>
<script type="text/javascript">
import checkbox from './VueComponents/checkbox.vue'

export default{
  name: 'request',
  components: { checkbox },
  props: ['markiert', 'active', 'items'],
  methods:{
    mark(item, index){
      if(item.text == '')
        this.edit(item, index)
      else
        this.$emit('mark')
    },
    edit(item, index){
      item.edit = !item.noteditable;
      if (!item.noteditable)
        this.edit_index = index;
      this.$forceUpdate();

    },
    enter(){
      if (this.tab)
        return
      console.log('enter');
      this.items.forEach(item => item.edit = false);
      this.$emit('values-change');
    },
    active_button(){

    },
    next(item, event){
      if (event) {
        event.preventDefault()
      }else
        console.log('null');
      this.tab = true
      let index = this.items.indexOf(item);
      if (index >= 0 && index < this.items.length - 1 ){
        for(const item of this.items)
          item.edit = false
        console.log('next1');
        let newIndex = index + 1
        let newItem = this.items[newIndex]
        this.edit(newItem, newIndex)
      }
    },
    unset_tab(){
      console.log('unset');
      this.tab = false
    }
  },
  data(){
    return{

      edit_index: -1,
      tab: false
    }
  },
  updated(){
    this.$nextTick(()=> {
      if(this.edit_index < 0)
        return;
      console.log('force update', this.edit_index);
      this.$refs.input[this.edit_index].focus();
    });
  }
}
</script>
<style media="screen" scoped>
.hide{
  display: none;
}
.row{
  display: flex;
  flex-direction: row;
  background-color: white;
}
.row.markiert{
  background-color: rgba(66, 185, 131, 0.5);
}
.cell{
  flex: 1 0 0;
  flex-direction: column;
}
.cell.name{
  flex-grow: 2;
}
.cell:first-child{
  flex-direction: row;
}
.cell a{
  flex: 1 0 auto;
  line-height: 40pt;
}
.cell input{
  font-size: 20pt;
  flex: 0 0 100%;
  width: 100%;
}
button{
  flex: 0 0 20pt;
}
</style>
