import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App.vue'
import VueSocketIOExt from 'vue-socket.io-extended';
import io from 'socket.io-client';

import requestPage from './components/requestPage.vue'
import loginpage from './components/VueComponents/loginpage.vue'
Vue.prototype.$shiftPressed = false;

const socket = io();

Vue.use(VueSocketIOExt, socket);

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  routes: [
    { path: '/request', name:'request', component: requestPage},
    { path: '/login', name:'login', component: {
        template: '<div><loginpage open_at_start="true" hide_register="true" v-on:login="this.login"/></div>',
        components: {loginpage} ,
        methods:{
          login(data){

            Vue.prototype.$socket.client.emit('login', data)
          }
        }
      }
    }
  ]
})

new Vue({
  render: h => h(App),
  router,

  components: {App},
  sockets: {
        connect: () => {
            console.log('socket connected')
        },
        login_failed: function(){
          console.log('hier');
          this.$router.push('login')
        }
      },
  methods:{
    login(){
    }
  },
  created(){
    this.$socket.$subscribe('connect', () => console.log('socket conn') )
    this.$socket.$subscribe('login_failed', ()=> {
      this.$router.push('/login')
    })
    this.$socket.$subscribe('login_success', data =>{
      this.$router.push('/request')
      this.$socket.client.emit('get_Table', data.token)
    })



    this.$router.push('/login')

  }
}).$mount('#app')



document.onkeydown = (e)=> {
  Vue.prototype.$shiftPressed = e.shiftKey;

  if(Vue.prototype.$copyline && ((e.ctrlKey||e.metaKey) && e.keyCode == 67))
    Vue.prototype.$copyline();
  if(Vue.prototype.$pasteline && ((e.ctrlKey||e.metaKey) && e.keyCode == 86))
    Vue.prototype.$pasteline();
  if(Vue.prototype.$pasteline && ((e.ctrlKey||e.metaKey) && e.keyCode == 88))
    Vue.prototype.$cutline();
}

document.onkeyup = (e)=>{
  if(e.key == 'Shift')
    Vue.prototype.$shiftPressed = false;
}
